body {
  font-family: "Dongle", sans-serif;
}

.card-text {
  font-size: 14pt;
  font-family: "Dongle", sans-serif;
}

.card-title.h5 {
  text-align: center;
  color: rgb(0, 132, 255);
  font-size: 26pt;
  font-family: "Dongle", sans-serif;
  background-color: transparent;
}

.MuVies {
  text-align: center;
  font-family: "Nunito", sans-serif;
  Color: bisque;
  background: linear-gradient(to left, rgb(66, 65, 65) 10%, rgb(39, 39, 39) 45%, rgb(26, 25, 25) 75%, rgb(73, 72, 72) 100%);
  width: 100vw;
  padding: 7px;
  box-shadow: inset 1px 1px 2px black, inset 2px 2px 3px black, inset 3px 3px 4px rgba(63, 62, 62, 0.877), inset -1px -1px 2px black, inset -2px -2px 3px black, inset -3px -3px 4px rgba(37, 37, 37, 0.384);
}